<template>
    <section>
        <ul>
            <li v-for="(todoItem, index) in propsdata" class="shadow">
                <i class="checkBtn fa fa-check" aria-hidden="true"></i><!-- 체크아이콘 -->
                    {{ todoItem }}
                <span class="removeBtn" type="button" @click="removeTodo(todoItem, index)">
                    <i class="fa fa-trash-o" aria-hidden="true"></i> <!-- 삭제버튼 -->
                </span>

            </li>
        </ul>
    </section>
</template>

<script>
export default {
    props : ['propsdata'],
    methods : {
        removeTodo(todoItem, index){
            this.$emit('removeTodo', todoItem, index);
        }
    }

}

</script>

<style scoped>
    ul{
        list-style-type: none;
        padding-left:0;
        margin-top:0;
        text-align: left;
    }
    li {
        display:flex;
        min-height: 50px;
        line-height: 50px;
        height:50px;
        margin:0.5rem 0;
        padding:0 0.9rem;
        background:#fff;
        border-radius:5px;
    }
    .checkBtn {
        line-height:45px;
        color:#62acde;
        margin:0 10px;
    }
    .removeBtn {
        margin-left:auto;
        padding:0 10px;
        color:#de4343;
        cursor:pointer;
    }

</style>



