<template>
    <div class="inputBox shadow">
        <input type="text" v-model="newTodoItem" placeholder="할 일을 입력하세요" v-on:keyup.enter="addTodo"> <!-- 엔터를 눌렀을 때 addtodo 실행 -->
        <span class="addContainer" v-on:click="addTodo">
            <i class="addBtn fa fa-plus" aria-hidden="true"></i>
        </span>

    </div>
</template>

<script>
export default {
    data(){
        return{
            newTodoItem:''
        }
    },
    methods : {

        addTodo(){
            if (this.newTodoItem !== ""){ // 인풋 박스의 입력 값이 있을 때만 저장
                var value = this.newTodoItem && this.newTodoItem.trim(); //인풋박스에 입력된 앞뒤 공백 문자열 제거
                this.$emit('addTodo',value);
                this.clearInput(); //인풋 박스의 입력 값 초기화
            }
        },
        clearInput(){
            this.newTodoItem='';
        }
    }
}
</script>

<style scoped>
    input:focus{
        outline:none;
    }
    .inputBox{
        background:#fff;
        height:50px;
        line-height: 50px;
        border-radius:5px;
    }
    .inputBox input {
        border-style:none;
        font-size:0.9rem;  width:60%;
    }
    .addContainer {
        float:right;
        background:linear-gradient(to right, #6478fb, #8763fb);
        display:inline-block;
        width:3rem;
        border-radius:0 5px 5px 0;
        cursor:pointer;
    }
    .addBtn {
        color:#fff;
        vertical-align: middle;

    }
</style>



