<template>
    <div class="clearAllContainer">
        <span class="clearAllBtn" @click="clearTodo">모두 지우기</span>
    </div>
</template>

<script>
export default {
    methods : {
        clearTodo(){
            this.$emit('removeAll');
        }
    }
}
</script>

<style scoped>
    .clearAllContainer {
        width:8.5rem;
        height:50px;
        line-height:50px;
        background:#fff;
        border-radius:10px;
        margin:0 auto;
    }
    .clearAllBtn {
        color:#e20303;
        display:black;
        font-size:12px;
        cursor:pointer;
    }
</style>



